import React, {useEffect, useState} from 'react';
import s from './Header.module.css'
import {fetching_API} from "../../Api/api";
import {useDispatch, useSelector} from "react-redux";
import {InitialStateType, setAuthorisedUsedAC} from "../../redux/reducers/auth-reduser";
import {AppRootStateType} from "../../redux/redux-store";
import ava from './../../images/ava.png'
import {Logo} from "./Logo";

export const Header = () => {
    const dispatch = useDispatch()
    const loggedUser = useSelector<AppRootStateType, InitialStateType>(state => state.authentication)

    useEffect(() => {

        fetching_API.getUserToken()
            .then(data => {
                dispatch(setAuthorisedUsedAC(data))
                return data
            })
            .then(data => {
                fetching_API.getUserAvatar(data.id)
                    .then(ava => {
                        if (ava.small) {
                            setAvatar(ava.small)
                        }
                    })
            })
            .catch(err => {
            })
            .finally()
    }, [])
    const [avatar, setAvatar] = useState(ava)
    return (
        <div className={s.header}>
            <div>
                <Logo/>
            </div>
            <div className={s.loggedUser}>
                <Avatar size={'60px'} isCircle={false} source={avatar}/>

                <div className={s.login}>
                    {loggedUser.isLogin
                        ? loggedUser.login
                        : <p>Login</p>}
                </div>
            </div>
        </div>
    );
};

type AvatarPropsType = {
    source: string
    size: string
    isCircle: boolean
}

export const Avatar: React.FC<AvatarPropsType> = React.memo(({size, isCircle, source}) => {
    const divStyle = {
        backgroundImage: `url(${source})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        width: size,
        height: size,
        borderRadius: isCircle ? '50%': '',
    }

    return (
        <section style={divStyle}>
        </section>
    )
})